@import url(https://fonts.googleapis.com/css2?family=Epilogue&family=Heebo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
  background-color: #D5EDF2;
}
.App {
  text-align: left;
  padding: 5% 15% 5% 15%;
  font-family: 'Heebo', sans-serif;
  letter-spacing: .01em;
  background-color: #ECEFF1;
  color:#ECEFF1;
}

h1{
  color: #2f3a3f;
  font-size: 3em;
}

h2{
  font-size: 2em;
}

h3{
  font-size: 1.5em;
}

h2,h3{
color: #2f3a3f;
}
.todoContainer{
  padding: 1.5em;
    border-bottom: 5px solid #D99C52;
  background-color: #BF7839;
}

.finished{
   background-color: #2f3a3f;
   border-bottom: 5px solid #75BFB2;
}

.todoTitle{
  font-size: 1.5em;
  text-transform: uppercase;
  letter-spacing: .03em;
}

@-webkit-keyframes strike{
  from{text-decoration: none;}
  to{text-decoration: line-through;}
}

@keyframes strike{
  from{text-decoration: none;}
  to{text-decoration: line-through;}
} 

.strike{
  -webkit-animation-name: strike; /* Chrome, Safari, Opera */
  -webkit-animation-duration: .5s; /* Chrome, Safari, Opera */
  animation-name: strike;
  animation-duration: .5s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.taskcreate{
  max-height: 0px;
  opacity: 0;
  transition: 700ms all;
  color:  #2f3a3f;
  line-height: 2em;
  font-weight: bold;
}

button{
background-color: #75BFB2;
border: 0;
padding: .5em;
border-radius: .2em;
margin: 1em 2em 0em 2em;
}

button:hover{
  background-color: #4D8184
}

.usecontainer{
  margin: 1em 0em 2em 0em;
}


.expand{
  opacity: 1;
  max-height: 500px;
}

.createnew{
  text-align: left;
  cursor: pointer;
}

.linecontainer{
  text-align: left;
}

.line{
  width: 50px;
  height: 8px;
  border-bottom: .5em solid #75BFB2;
  position: relative;
  display: block;
  left: 0px;
  top: 22px;
  text-align: right;
  }


  .line2{
    width: 50px;
    height: 8px;
    border-bottom: .5em solid #75BFB2;
    transform: rotate(90deg);
    position: relative;
    left: 5px;
    top: 10px;
    display: block;
    text-align: right;
    opacity: 1;
    transition: transform .4s ease-in-out, top .4s ease-in-out, right .4s ease-in-out, opacity .4s ease-in-out;
  }

  .grid{
    display: grid;
    grid-gap: 5%;
    grid-template-columns: .1fr 1fr;
    height: 5%;
  }

  label{

  }

  .shifted{
    transform: rotate(0deg);
    top: 6px;
    left: 0px;
    border-bottom: .5em solid #BF7839;
  }

